    methods: {
        sendReport() {
            console.log(this.reportReason);
        },
        formatTimestamp(timestamp) {
            const date = new Date(timestamp * 1000);
            const isGerman = i18n.global.locale.value === 'de';
            const options = {
                day: '2-digit',
                month: 'long',
                year: '2-digit',
                hour: '2-digit',
                minute: '2-digit',
                hour12: !isGerman,
                timeZone: 'CET',
                timeZoneName: 'short'
            };
            const lang = isGerman ? 'de-DE' : 'en-US';
            return new Intl.DateTimeFormat(lang, options).format(date);
        }
    }
}
</script>
