<script>
import { useAppVariableStore } from '@/shared/stores/app-variable';
import jsVectorMap from 'jsvectormap';
import 'jsvectormap/dist/maps/world.js';
import 'jsvectormap/dist/jsvectormap.min.css';

export default {
    props: ['data'],
    data() {
        return {
            appVariable: useAppVariableStore(),
        }
    },
    methods: {
        getMarkerColor(state) {
            switch (state) {
                case 1:
                    return 'green';
                case 2:
                    return 'red';
                case 3:
                    return 'orange';
                default:
                    return 'gray';
            }
        },
        renderMap() {
            const markers = this.data.map(item => ({
                name: item.name,
                coords: item.coords,
                style: {
                    initial: {
                        fill: this.getMarkerColor(item.state)
                    }
                }
            }));

            const map = new jsVectorMap({
                selector: '#map',
                map: 'world',
                zoomButtons: false,
                normalizeFunction: 'polynomial',
                hoverOpacity: 0.5,
                hoverColor: false,
                zoomOnScroll: false,
                series: {
                    regions: [{
                        normalizeFunction: 'polynomial'
                    }]
                },
                labels: {
                    markers: {
                        render: (marker) => marker.name
                    }
                },
                focusOn: {
                    x: 0.5,
                    y: 0.5,
                    scale: 1
                },
                markers: markers,
                onRegionTooltipShow(event, tooltip, code) {
                    tooltip.hide();
                },
                markerStyle: {
                    initial: {
                        fill: this.appVariable.color.theme,
                        r: 5,
                    },
                    hover: {
                        fill: this.appVariable.color.info
                    }
                },
                markerLabelStyle: {
                    initial: {
                        fontFamily: this.appVariable.font.bodyFontFamily,
                        fontSize: '12px',
                        fill: this.appVariable.color.bodyColor
                    },
                },
                regionStyle: {
                    initial: {
                        fill: this.appVariable.color.gray600,
                        fillOpacity: .5,
                        stroke: 'none',
                        strokeWidth: 0.4,
                        strokeOpacity: 1
                    },
                    hover: {
                        fillOpacity: .25
                    }
                },
                backgroundColor: 'transparent',
            });
        }
    },
    mounted() {
        this.renderMap();
    },
}
</script>
<template>
    <div id="map" style="height: 400px; left: 3%"></div>
</template>